@import "variables";

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;1,400&display=swap');
body{
	background-color:$colorGris;
}

@mixin botones{
background-color:$colorDorado;
color:$colorAzul;
border-radius:4px;
padding:0.2em;
}

@mixin tarjetas{
border-radius: 1em;
box-shadow: $sombraCard;
background-color: $colorAzul;
color:$colorDorado;
font-family: 'Montserrat', sans-serif;
border-style: none;
text-decoration: none;
}

header{
background:$gradiente1;
min-height:80px;
margin-bottom:1.5em;
display:flex;
align-items:center;
justify-content: center;
width: 100%;
border-radius: 0 0 12px 12px;
box-shadow: $sombraCard;
	.navbar{
	width: 80%;
	margin:0;
		.navbar-brand{
		padding:0;
			.logoM{
			width: 7em;
			margin-left:1em;
			}
		}
		.nav-item {
			.nav-link{
			color:$colorGris;
			font-family: 'Montserrat', sans-serif;
			font-size: 1em;
			padding-left:0.8em;
			}
		}
			.navbar-nav{
		}
		#dropdownMenuLink{
		background: none;
		border-style:none;
		color:$colorGris;
		font-family: 'Montserrat', sans-serif;
		font-size: 1em;
		padding-left: 0.8em;
		margin-top: 0.15em;
		}

		.dropdown-menu{
		background:$gradiente2;
		border-style:none;
		margin-top:1em;
		border-radius:0 0 1em 1em;	
			.dropdown-item{
			color:$colorGris;
			}
		}		
		.navbar-toggler{
		opacity:30%;
		background-color: #ACA9A4;
		margin-top:0.5em;
		border-radius:200px;
		}
 		.navbar-collapse{
 		margin-left:1em;
 		width:80%;
 		}
 		#navbarNav{
		margin-top:0.5em;	
		margin-left:1em;	
			}	
	}
}	

.mainInicio{
width: 100%; 
justify-content: center;
	#carouselExampleControls{
	width: 100%;
		.carousel-inner{
		border-radius: 1em;
		box-shadow: $sombraCard;	
		}
	}	
	.textoInicio{
		.container{		
		width: 80%;	
		margin-top:1.2em;
			h2{
			color:$colorAzul;
			font-family: $fuente;
			font-weight: bold;
			font-size: 1.3em;
			}
			p{
			color:$colorAzul;
			font-family: $fuente;
			font-size: 1.1em;
			}
		}
	}		
	.serviciosInicio{
		.container{
		
		width: 80%;	
		margin-top:1em;
		
			.row{
			display:flex;	
			justify-content: space-evenly;
				.tarjetaInicio{
				@include tarjetas;
				max-width: 14rem;
				padding:0;
				margin-bottom: 1em;

					.card-img-top{
		 			border-radius:1em;
		 			width: 14rem;
					}

					.card-text{
					text-decoration: none;
					color:$colorDorado;
					}

				}
			}	
		}
	}	
}


footer{
margin-top:1.5em;	
background: $gradiente2;
height: 80px;
border-radius:12px 12px 0 0;
width: 100%;
justify-content: center;
	.container{
	justify-content: center;
	height: 100%;
		.row{
		height: 100%;
		width:100%;
		align-items:center;
		justify-content: center;
		margin-left: 0;


			p{
			font-family: $fuente;
			font-size: 1em;
			color: $colorDorado;
			margin-bottom:0;
			}
			.logoSocial{
			margin-left:0.6em;
			}
		}
	}
}
.mainAsesoriamiento{

	.container{
	width:95%;
	justify-content: center;	
	}
	.imgAsesoramiento{
	height:30em;
	border-radius:$bordeCard;
	background-image:url("../media/asesoramientoDeco.jpg");
	background-size:cover;
	box-shadow:$sombraCard;
	}

}		
 .mainServicios{
	height:36em;
	margin-top: 1.5em;
	margin-left: 25em;
	margin-bottom: 1.5em;
	display: grid;
	grid-template-columns:40% 40%;
	grid-template-rows: 230px 30px 230px 30px;
	grid-row-gap: 12px;
	grid-column-gap: 20px;
	grid-template-areas:
	"muebles restauracion"
	"tituloMuebles tituloRestauracion"
	"remodelacion obras"
	"tituloRemodelacion tituloObras";
	
		.muebles{
		grid-area: muebles;
		background-color:white;
		border-radius: 0.6em;
		background-image:url("../media/muebles a medida.jpg");
		background-size:cover;
		}
		.tituloServicios{
		font-family:$fuente;
		color:$colorAzul;
			a{
				@include botones;
			}	
		}
		.restauracion{
		grid-area: restauracion;
		background-color:white;
		border-radius: 0.6em;
		background-image:url("../media/restaurado.jpg");
		background-size:cover;
		}
		.remodelacion{
		grid-area: remodelacion;
		background-color:white;
		border-radius: 0.6em;
		background-image:url("../media/remodelaciones.jpg");
		background-size:cover;
		}
		.obras{
		grid-area:obras;
		background-color: white;
		border-radius: 0.6em;
		background-image:url("../media/peque√±as obras.jpg");
		background-size:cover;
		}
	}	
